import React from 'react';
import { View, StyleSheet } from 'react-native';
import Modal from 'react-native-modal';
import { Button, Text } from 'tradewize-component';
import colors from '../../../theme/colors';
import { SvgXml } from 'react-native-svg';
import {
  IconStateSuccess,
  IconStateWarning,
  IconStateError,
} from '../../../assets/svg';
import { useTranslation } from 'react-i18next';
import { useAppSelector, useAppDispatch } from '../../../redux/store/types';
import { AppAlertModalProps } from '../../../redux/features/app/types';
import { hideAlert } from '../../../redux/features/app/actions';

const AppAlertModal = () => {
  const dispatch = useAppDispatch();
  const {
    visible,
    type,
    title,
    message = '',
    buttonText,
    onClose,
    showIcon = true,
    customIcon,
    buttonVariant = 'primary',
    buttonStyle,
    messageStyle,
    titleStyle,
    isShowIcon = true,
    isBackdropPress = true,
    customContent,
  }: AppAlertModalProps = useAppSelector(state => state.app.alertModal);

  const { t } = useTranslation();

  const handleClose = () => {
    // Gọi onClose callback nếu có
    if (onClose) {
      onClose();
    }
    // Luôn dispatch action để hide alert
    dispatch(hideAlert());
  };

  const getIcon = () => {
    if (!showIcon) return null;
    if (customIcon) return <SvgXml xml={customIcon} width={65} height={65} />;
    switch (type) {
      case 'warning':
        return <SvgXml xml={IconStateWarning()} width={65} height={65} />;
      case 'error':
        return <SvgXml xml={IconStateError()} width={65} height={65} />;
      default:
        return <SvgXml xml={IconStateSuccess()} width={65} height={65} />;
    }
  };

  const getTitleColor = () => {
    switch (type) {
      case 'warning':
        return colors.warning;
      case 'error':
        return colors.error;
      default:
        return colors.success;
    }
  };

  const getDefaultTitle = () => {
    switch (type) {
      case 'warning':
        return t('WARNING');
      case 'error':
        return t('ERROR');
      default:
        return t('SUCCESS');
    }
  };

  return (
    <Modal
      isVisible={visible}
      animationIn="zoomIn"
      animationOut="zoomOut"
      backdropOpacity={0.5}
      useNativeDriver
      onBackdropPress={isBackdropPress ? handleClose : undefined}
    >
      <View style={styles.container}>
        {customContent ? (
          customContent
        ) : (
          <>
            {isShowIcon && getIcon()}
            <Text
              style={[styles.title, { color: getTitleColor() }, titleStyle]}
            >
              {title || getDefaultTitle()}
            </Text>
            {message ? (
              <Text style={[styles.message, messageStyle]}>{message}</Text>
            ) : null}
          </>
        )}

        <Button
          variant={buttonVariant}
          style={[styles.button, buttonStyle]}
          onPress={handleClose}
        >
          <Text style={styles.buttonText}>{buttonText || t('CLOSE')}</Text>
        </Button>
      </View>
    </Modal>
  );
};

const styles = StyleSheet.create({
  container: {
    backgroundColor: colors.white,
    borderRadius: 12,
    padding: 24,
    alignItems: 'center',
  },
  title: {
    fontSize: 22,
    fontWeight: '700',
    marginBottom: 8,
    textAlign: 'center',
    marginTop: 6,
    lineHeight: 28,
  },
  message: {
    fontSize: 14,
    color: colors.black,
    textAlign: 'center',
    lineHeight: 20,
    marginBottom: 20,
  },
  button: {
    backgroundColor: colors.mainColorPrimary,
    width: '100%',
  },
  buttonText: {
    color: colors.white,
    fontWeight: '600',
    fontSize: 16,
    lineHeight: 24,
  },
});

export default AppAlertModal;
